import React, { useState, useEffect, useRef, useCallback } from 'react'
import clsx from 'clsx'
import { v4 as uuid } from 'uuid'

import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'

import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Paper from '@material-ui/core/Paper'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import Typography from '@material-ui/core/Typography'

import AddIcon from '@material-ui/icons/AddCircle'
import EditIcon from '@material-ui/icons/Edit'
import ImportIcon from '@material-ui/icons/CloudDownload'
import CopyIcon from '@material-ui/icons/FileCopy'

import Collapsible, { CollapsibleSummary, CollapsibleDetails } from '../../../components/Collapsible'
import EnhancedTable from '../../../components/EnhancedTable'
import Inputs from '../../../components/Inputs'

import { sortObject } from '../../../helpers/utils'
import DevicesPage from './DevicesPage'
import ImportDevicesPage from './ImportDevicesPage'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	deleteButton: {
		marginTop: '25px'
	},
	hr: {
		margin: '10px 2px'
	},
	heading: {
		marginRight: theme.spacing()
	},
	CustomTextInput: {
		width: 150
	},
	CustomDialogTitle: {
		padding: 0,
		marginRight: '50px'
	},
	TableCell: {
		verticalAlign: 'top',
		padding: '5px 0'
	},
	divider: {
		margin: 'auto',
	},
	paper: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(1),
		marginBottom: theme.spacing(1),
		minHeight: '64px',
		display: 'flex',
		alignItems: 'center',
		
		margin: '0 auto',
		position: 'fixed',
		zIndex: 999,
		right: 0,
		left: 0,
	},
	newlyCreatedDevice: {
		backgroundColor: 'rgba(99, 233, 30, 0.08) !important'
	},
	ListItem: {
		padding: 0
	},
	DeviceList: {
		paddingTop: '72px'
	}
}));


i18n.setTranslations(language => require('./i18n/' + language));

const DevicesListPage = React.memo(props => {
	Log._('DevicesListPage', 'RENDER DevicesListPage', 'development');
	const classes = useStyles();
	
	
	/**
	 *
	 */
	const _groupDevices = (groupBy, d) => {
		
		let g = {}
		for (let deviceId in d) {
			let device = d[deviceId];
			
			// by manufacturer
			if (groupBy === 'manufacturer') {
				const manufacturer = device.attributes && device.attributes.manufacturer ? device.attributes.manufacturer.name : ('(' + i18n.t('no manufacturer') + ')');
				g[manufacturer] = g[manufacturer] || [];
				g[manufacturer].push(device);
			}
			
			// by function
			else {
				const func = i18n.t(device.function);
				g[func] = g[func] || [];
				g[func].push(device);
			}
		}
		
		return sortObject(g, false);
	}
	
	
	const [selectedDevice,setSelectedDevice] = useState(null);
	const [importDevices,setImportDevices] = useState(null);
	
	const devices = useRef(props.devices);
	const groupBy = useRef(props.settings.configGroupDevices || 'function');
	const lastDeviceAdded = useRef();
	const filterBy = useRef();
	const filterByTimeout = useRef();
	
	const [groups,setGroups] = useState(_groupDevices(groupBy.current, props.devices));
	const [width,setWidth] = useState('100%');
	
	
	// 
	useEffect(() => {
		devices.current = props.devices;
		setGroups(_groupDevices(props.settings.configGroupDevices || 'function', props.devices));
		
	}, [props.devices, props.settings]);
	
	// 
	useEffect(() => {
		props._onDevicesChange && props._onDevicesChange(devices.current);
		
	}, [groups, props]);
	
	// 
	useEffect(() => {
		if (lastDeviceAdded.current) {
			document.getElementById('DialogContent').scrollTop = (document.getElementById('device-' + lastDeviceAdded.current) && document.getElementById('device-' + lastDeviceAdded.current).offsetTop) || 0;
		}
		
	}, [groups]);
	
	
	/**
	 *
	 */
	const _importDevices = importDevices => _saveDevices({ ...devices.current, ...importDevices });
	
	/**
	 *
	 */
	const _saveDevice = saveDevice => {
		lastDeviceAdded.current = saveDevice.id;
		_saveDevices({ ...devices.current, [saveDevice.id]: { ...saveDevice, 'hash': Date.now() } });
	}
	
	/**
	 *
	 */
	const _saveDevices = saveDevices => {
		devices.current = saveDevices;
		_filterBy(filterBy.current, saveDevices);
		
		setSelectedDevice(null);
		setImportDevices(null);
	}
	
	/**
	 *
	 */
	const _copyDevice = device => {
		const id = device.id.substr(-6).substr(0,1) === '_' ? device.id.substr(0, device.id.lastIndexOf('_')) + '_' + uuid().substr(0,5) : device.id + '_' + uuid().substr(0,5);
		devices.current[id] = { ...device, 'name': device.name + ' (' + i18n.t('duplicate') + ')', 'id': id, 'hash': Date.now() }
		
		_saveDevices(devices.current);
	}
	
	/**
	 *
	 */
	const _deleteDevice = deviceId => {
		_deleteInWidgets(deviceId);
		delete devices.current[deviceId];
		
		_saveDevices(devices.current);
	}
	
	/**
	 *
	 */
	const _deleteInWidgets = (deviceId, stateKey = null) => {
		let layout = [ ...props.layout ];
		
		// drawer
		for (let drawerKey of Object.keys(layout)) {
			let drawer = layout[drawerKey];
			
			// tabs
			for (let tab of drawer.tabs) {
				tab.columns = tab._fullscreen !== undefined ? [[tab._fullscreen]] : tab.columns;
				
				// columns
				for (let columnIndex in tab.columns) {
					let column = tab.columns[columnIndex];
					
					// widgets
					for (let widget of column) {
						widget = widget || { devices: [] }
						
						// device deletion
						if (stateKey === null) {
							widget.devices = widget.devices.filter(d => d.deviceId !== deviceId);
						}
						
						// state deletion
						else {
							widget.devices = widget.devices.map(d => {
								
								// remove from secondary state keys
								if (d.deviceId === deviceId && stateKey !== null && Array.isArray(d.secondaryStatesKeys) && d.secondaryStatesKeys.length > 0) {
									d.secondaryStatesKeys = d.secondaryStatesKeys.filter(secondaryStateKey => secondaryStateKey !== stateKey);
								}
								
								// remove from primary and body state key
								d.primaryStateKey = d.primaryStateKey !== stateKey ? d.primaryStateKey : '';
								d.bodyStateKey = d.bodyStateKey !== stateKey ? d.bodyStateKey : '';
								
								return d;
							});
						}
					}
				}
				
			}
		}
		
		props._onLayoutsChange(layout);
	}
	
	/**
	 *
	 */
	const _groupBy = (p, val) => {
		groupBy.current = val;
		setGroups(_groupDevices(val, devices.current));
		props._onLayoutsChange(null, { 'configGroupDevices': val });
	}
	
	/**
	 *
	 */
	const _filterBy = (val = '', d) => {
		filterBy.current = val;
		val = val.toLowerCase();
		
		clearTimeout(filterByTimeout.current);
		filterByTimeout.current = setTimeout(() => {
			
			// get filter params
			const filters = val.match(RegExp(/-(id|name|function|stateId|stateLabel|state|action|manufacturer):[^\\-]*/g));
			let labels = []
			let values = {}
			
			filters && filters.forEach(filter => {
				let [ label, value ] = filter.substr(1).split(':');
				labels.push(label.trim());
				values[label.trim()] = value.trim().toLowerCase();
			});
			
			
			// filter devices
			let result = {}
			Object.values(d).forEach(device => {
				
				// filter by device
				if (
						((!filters || labels.indexOf('id') > -1) && device.id.toLowerCase().indexOf(values['id'] || val) > -1) ||
						((!filters || labels.indexOf('name') > -1) && device.name.toLowerCase().indexOf(values['name'] || val) > -1) ||
						((!filters || labels.indexOf('function') > -1) && device.function.toLowerCase().indexOf(values['function'] || val) > -1) ||
						((!filters || labels.indexOf('function') > -1) && i18n.t(device.function).toLowerCase().indexOf(values['function'] || val) > -1) ||
						((!filters || labels.indexOf('manufacturer') > -1) && device.attributes && device.attributes.manufacturer && device.attributes.manufacturer.name.toLowerCase().indexOf(values['manufacturer'] || val) > -1)
					) {
					
					result[device.id] = device;
				}
				
				// filter by device states
				if (result[device.id] === undefined) {
					device.states = device.states || {}
					
					for (let stateKey in device.states) {
						let state = device.states[stateKey]
						state.label = state.label || '';
						
						if (
								((!filters || labels.indexOf('stateId') > -1) && stateKey.toLowerCase().indexOf(values['stateId'] || val) > -1) ||
								((!filters || labels.indexOf('stateLabel') > -1) && state.label.toLowerCase().indexOf(values['stateLabel'] || val) > -1) ||
								((!filters || labels.indexOf('state') > -1) && state.state && typeof state.state !== 'object' && state.state.toLowerCase().indexOf(values['state'] || val) > -1) ||
								((!filters || labels.indexOf('action') > -1) && state.action && typeof state.action !== 'object' && state.action.toLowerCase().indexOf(values['action'] || val) > -1) ||
								((!filters || labels.indexOf('state') > -1) && state.state && typeof state.state === 'object' && state.state.node.toLowerCase().indexOf(values['state'] || val) > -1) ||
								((!filters || labels.indexOf('action') > -1) && state.action && typeof state.action === 'object' && state.action.node.toLowerCase().indexOf(values['action'] || val) > -1)
							) {
							
							result[device.id] = device;
						}
					}
				}
			});
			
			setGroups(_groupDevices(groupBy.current, result));
			
		}, 750);
	}
	
	/*
	 *
	 */
	let groupedDevices = [];
	for (let key in groups) {
		const labelId = key.toLowerCase().replace(/ /g, '');
		
		groupedDevices.push(
			<ListItem dense classes={{ root: classes.ListItem }} key={'group-' + labelId}>
				<ListItemText disableTypography>
					<Group
						id={labelId}
						label={key}
						devices={groups[key]}
						_selectDevice={device => setSelectedDevice(device.id)}
						_copyDevice={_copyDevice}
						/>
					
				</ListItemText>
			</ListItem>
		);
	}
	
	// adjust width
	const paperComponent = useCallback(el => {
		if (el !== null) {
			const boxWidth = el.parentNode.offsetWidth-20;
			setWidth(boxWidth);
		}
	}, []);
	
	return (
		<>
			<Paper ref={paperComponent} className={clsx(classes.paper, 'DeviceListFilter')} style={{ width: width }}>
				<Typography classes={{ root: classes.heading }} display="inline" variant="h6">
					{i18n.t('Devices List') + ' (' + Object.keys(devices.current).length + ')'}
				</Typography>
				
				<Inputs.Select
					key="groupBy"
					label="Group by"
					width={150}
					
					onChange={_groupBy}
					val={groupBy.current}
					selection={[
						{
							'function': 'Function'
						},
						{
							'manufacturer': 'Manufacturer'
						}
					]}
					/>
				
				<Inputs.Text
					key="filterBy"
					id="filterBy"
					width={400}
					
					label="Filter"
					info="DevicesListPage#filterBy#info"
					link="https://forum.iobroker.net/post/542452"
					
					onChange={(props, val) => _filterBy(val, devices.current)}
					val={filterBy.current}
					//placeholder={i18n.t('Filter') + '..'}
					/>
				
				<Divider
					classes={{ root: classes.divider }}
					orientation="vertical"
					/>
				
				<Inputs.Button
					key="device-add"
					className="deviceAdd"
					onClick={() => setSelectedDevice('#new')}
					startIcon={<AddIcon />}
					disabled={selectedDevice !== null || importDevices !== null}
					label='add device'
					/>
				
				<Inputs.Button
					key="devices-import"
					className="deviceImport"
					onClick={() => setImportDevices(true)}
					startIcon={<ImportIcon />}
					disabled={selectedDevice !== null || importDevices !== null}
					label='import devices'
					/>
				
			</Paper>
			
			{selectedDevice != null && <DevicesPage
											device={selectedDevice === '#new' ? { 'id': uuid(), 'function': 'other', 'states': {}, 'options': {}, 'attributes': { '_created': Date.now() }} : devices.current[selectedDevice]}
											onSave={_saveDevice}
											onDelete={_deleteDevice}
											onStateDelete={_deleteInWidgets}
											onClose={() => setSelectedDevice(null)}
											/>}

			{importDevices !== null && <ImportDevicesPage
											devices={devices.current}
											onImport={_importDevices}
											onClose={() => setImportDevices(null)}
											/>}
			
			<List className="DeviceList" classes={{ root: classes.DeviceList }} dense>
				{groupedDevices}
			</List>
		</>
	)

}, (prevProps, nextProps) => nextProps.selected !== true); // re-render on false (not equal), only render when selected

export default DevicesListPage;


/*
 *
 */
const Group = props => {
	const { id, label, devices } = props;
	
	return (
		<Collapsible key={'DeviceStates-' + id + '-' + devices.length} className="Group">
			<CollapsibleSummary>{label + ' (' + devices.length + ')'}</CollapsibleSummary>
			<CollapsibleDetails>
				<DeviceTable {...props} />
			</CollapsibleDetails>
		</Collapsible>
	);
}

/*
 *
 */
const DeviceTable = props => {
	Log._('DevicesListPage', 'RENDER DeviceTable', 'development');
	const { devices } = props;
	
	return (
		<EnhancedTable
			toolbar={false}
			headCells={[
				{ id: 'edit', width: '3%', disablePadding: true, label: '' },
				{ id: 'copy', width: '2%', disablePadding: true, label: '' },
				{ id: 'name', width: '15%', label: i18n.t('Name') },
				{ id: 'manufacturer', width: '15%', label: i18n.t('Manufacturer') },
				{ id: 'function', width: '10%', label: i18n.t('Function') },
				{ id: 'states', width: '55%', disablePadding: false, label: i18n.t('States') },
				//{ id: 'options', width: '20%', disablePadding: false, label: i18n.t('Options') },
				//{ id: 'attributes', width: '15%', disablePadding: false, label: i18n.t('Attributes') }
			]}
			rows={Object.values(devices)}
			
			defaultOrderBy="name"
			selectable={false}
			sortable={true}
			stickyHeader
			
			CustomTableBody={CustomTableProps => <DeviceList {...props} {...CustomTableProps} />}
			/>
	);
	
}

/*
 *
 */
const DeviceList = props => {
	Log._('DevicesListPage', 'RENDER DeviceList', 'development');
	const classes = useStyles();
	const { sortedRows, _selectDevice, _copyDevice } = props;
	
	return (
		<TableBody>
		
		{sortedRows.map((device, index) => {
			//let options = Object.keys(device.options || {}).filter(optionKey => (device.options[optionKey] === '' || (Array.isArray(device.options[optionKey]) && device.options[optionKey].length === 0) || (typeof device.options[optionKey] === 'object' && Object.keys(device.options[optionKey]).length === 0)) === false).sort().join(', '); 
			//let attributes = Object.keys(device.attributes || {}).sort().join(', ');
			
			// skip
			if (device.attributes && device.attributes.ignore === true) {
				return null;
			}
			
			return (
				<TableRow
					key={'TableRow-' + index}
					id={'device-' + device.id}
					hover
					tabIndex={-1}
					selected={device.attributes && device.attributes._created > Date.now()-24*60*60*1000}
					classes={{ selected: classes.newlyCreatedDevice, root: "Device" }}
					>
					
					<TableCell padding="checkbox" scope="row" align="center">
						<IconButton onClick={() => _selectDevice(device)}>
							<EditIcon />
						</IconButton>
					</TableCell>
					<TableCell padding="checkbox" align="center">
						<IconButton onClick={() => _copyDevice(device)}>
							<CopyIcon />
						</IconButton>
					</TableCell>
					<TableCell>{device.name + (device.label ? ' (' + device.label + ')' : '')}</TableCell>
					<TableCell>{(device.attributes && device.attributes.manufacturer && device.attributes.manufacturer.name) || ''}</TableCell>
					<TableCell>{i18n.t(device.function)} (<code>{device.function}</code>)</TableCell>
					<TableCell>{Object.keys(device.states || {}).join(', ')}</TableCell>
					{/*<TableCell>{options.length > 35 ? <Tooltip title={options}><span>{options.substr(0,32) + '...'}</span></Tooltip> : options}</TableCell>*/}
					{/*<TableCell>{attributes.length > 35 ? <Tooltip title={attributes}><span>{attributes.substr(0,32) + '...'}</span></Tooltip> : attributes}</TableCell>*/}
				</TableRow>
			
			);
		})}
		
		</TableBody>
	);
	
}