export default {
	"DevicesListPage#filterBy#info": "The search is case-insensitive and will return results for both \"cd\" oder \"CD\". A wildcard (*) is not supported but searching \"CD\" will find \"ABCDE\" as well as \"ABCD\" or \"CDEF\"." +
										"The following attributes are searched holistically: Name of device (name), ID of device (id), Function of device (function), ID of its states (stateId), label of its states (stateLabel), State as well as Action Trigger of its states (state / action), Manufacturer (manufacturer). The fields may be searched separately via e.g. \"-name:CD\" or \"-function:light\". This may be combined via e.g. \"-name:PC -function:light\"."
}