import React, { useState, useEffect, useRef } from 'react'
import clsx from 'clsx'

import Cache from '../../../controller/Cache'
import Connection from '../../../controller/Connection'
import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'
import timeConverter from '../../../helpers/timeConverter'

import LinearProgress from '@material-ui/core/LinearProgress'
import Divider from '@material-ui/core/Divider'
import Paper from '@material-ui/core/Paper'
import TreeItem from '@material-ui/lab/TreeItem'
import TreeView from '@material-ui/lab/TreeView'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'

import CollapseIcon from '@material-ui/icons/ArrowDropDown'
import ExpandIcon from '@material-ui/icons/ArrowRight'
import GroupIcon from '@material-ui/icons/Folder'
import RefreshIcon from '@material-ui/icons/Refresh'
import StateIcon from '@material-ui/icons/BookmarkBorder'

import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	divider: {
		flexGrow: 1
	},
	TreeView: {
		padding: theme.spacing(2),
		'&:focus > $TreeItemContent $TreeItemLabel, &:hover > $TreeItemContent $TreeItemLabel, &$TreeItemSelected > $TreeItemContent $TreeItemLabel': {
			backgroundColor: 'transparent',
		},
	},
	TreeItem: {
		color: theme.palette.text.secondary,
		'&:hover > $TreeItemContent': {
			backgroundColor: theme.palette.action.hover,
		},
	},
	TreeItemContent: {
		color: theme.palette.text.secondary,
		paddingRight: theme.spacing(1),
		//fontWeight: theme.typography.fontWeightMedium,
		'$TreeItemExpanded > &': {
			//fontWeight: theme.typography.fontWeightRegular,
		},
	},
	TreeItemExpanded: {
	},
	TreeItemSelected: {
		fontWeight: 'bold !important',
	},
	TreeItemGroup: {
		fontWeight: 'initial',
		'& $TreeItemContent': {
			paddingLeft: theme.spacing(2),
		},
	},
	TreeItemLabel: {
		fontWeight: 'inherit',
		color: 'inherit',
	},
	Entry: {
		display: 'flex',
		alignItems: 'center',
		padding: theme.spacing(0.5, 0),
	},
	Group: {
		//fontWeight: 'bold'
	},
	EntryIcon: {
		marginRight: theme.spacing(1),
	},
	EntryText: {
		fontWeight: 'inherit',
		flexGrow: 1,
	}
}));


/*
 *
 */
const indexObjects = (list, index) => {
	let objectsTree = {}
	let root = [];
	
	for (let objectId of list) {
		
		// index state
		objectsTree[objectId.substr(0, objectId.lastIndexOf('.'))] = objectsTree[objectId.substr(0, objectId.lastIndexOf('.'))] || {}
		objectsTree[objectId.substr(0, objectId.lastIndexOf('.'))][objectId] = index[objectId];
		
		// make sure state is added to all its parents
		let namespace = objectId;
		while (namespace.indexOf('.') > -1) {
			const id = namespace;
			namespace = namespace.substr(0, namespace.lastIndexOf('.'));
			
			if (objectsTree[namespace] === undefined || objectsTree[namespace][id] === undefined) {
				objectsTree[namespace] = objectsTree[namespace] || {}
				objectsTree[namespace][id] = {}
			}
			
			if (namespace && namespace.indexOf('.') === -1 && root.indexOf(namespace) === -1) {
				root.push(namespace);
			}
		}
	}
	
	return [ root, objectsTree ];
}

/*
 *
 */
const StateExplorer = React.memo(props => {
	const { val, _onChange } = props;
	const classes = useStyles();
	
	
	const [objectList,setObjectList] = useState(Cache.get('objectList') || null);
	const selectedItem = useRef(val);
	const searchTimeout = useRef();
	
	useEffect(() => {
		
		if (objectList === null || !objectList.root) {
			const socket = Connection.connection;
			socket.getObjects()
				.then(res => {
					let list = [];
					let index = {}
					
					res.forEach((promise, i) => {
						
						if (promise.status === 'fulfilled' && promise.value && Array.isArray(promise.value.rows)) {
							
							// read results and sort
							for (let obj of promise.value.rows) {
								
								// skip system
								if (obj.id.substr(0,6) === 'system') {
									continue;
								}
								
								// index result
								index[obj.id] = obj.value;
								list.push(obj.id);
							}
						}
					});
					
					// index sorted results
					list.sort();
					const [ root, tree ] = indexObjects(list, index);
					
					const _objectList = {
						'updated': Date.now(),
						'root': root.sort(),
						'list': list,
						'tree': tree
					}
					
					Cache.set('objectList', _objectList);
					setObjectList(_objectList);
				})
				.catch(error => {
					Log._('StateExplorer', error.message, 'error', true, error.stack);
				});
		}
		
	}, [objectList]);
	
	//
	const search = (props, val) => {
		clearTimeout(searchTimeout.current);
		searchTimeout.current = setTimeout(() => {
			const data = Cache.get('objectList');
			
			const [ root, tree ] = indexObjects(data.list.filter(obj => obj.indexOf(val) > -1), data.tree);
			setObjectList(prevObjectList => ({
				...prevObjectList,
				'root': root.sort(),
				'tree': tree
			}));
			
		}, 750);
	}
	
	//
	const selectObject = (e, val) => {
		const selected = objectList.tree[val] || null; // tree only holds channels, thus null will be a state
		
		if (selected === null) {
			selectedItem.current = val;
			_onChange(val);
		}
	}
	
	//
	const renderTree = key => {
		let states = [];
		let children = [];
		
		// channels
		if (objectList.tree[key]) {
			Object.keys(objectList.tree[key]).forEach(channelKey => {
				const child = objectList.tree[key][channelKey];
				
				if (child.type === 'state') {
					states.push(channelKey);
				}
				else {
					children.push(renderTree(channelKey));
				}
			})
		}
		
		// states
		if (states.length > 0) {
			children = children.concat(
				states.map(stateKey => <Item key={stateKey} nodeId={stateKey} label={<Entry key={stateKey} id={stateKey.substr(stateKey.lastIndexOf('.')+1)} {...objectList.tree[stateKey.substr(0, stateKey.lastIndexOf('.'))][stateKey]} />} />)
			);
		}
		
		const channel = key.substr(key.lastIndexOf('.')+1);
		const details = key.indexOf('.') > -1 ? objectList.tree[key.substr(0, key.lastIndexOf('.'))][key] : {}
		return (
			<Item key={key} nodeId={key} children={children} label={<Entry key={key} group id={channel} {...details} />} />
		);
	}
	
	return (
		<Paper>
			<Inputs.Fieldset>
				<Inputs.Text
					id='search'
					label='Search'
					onChange={search}
					/>
				
				<div className={classes.divider} />
				<div style={{ margin: 'auto 0' }}>
					{objectList !== null && <span style={{ margin: 'auto 0' }}>{i18n.t('Last refresh') + ': ' + timeConverter.parseDate(objectList.updated, 'dd.MM.yyyy, HH:mm:ss')}</span>}
					<Inputs.Button
						label='refresh'
						startIcon={<RefreshIcon />}
						onClick={() => setObjectList(null)}
						/>
				</div>
			</Inputs.Fieldset>
			<Divider />
			
			{(objectList === null || !objectList.root) ?
			<LinearProgress />
			:
			<TreeView
				className={classes.TreeView}
				defaultCollapseIcon={<CollapseIcon />}
				defaultExpandIcon={<ExpandIcon />}
				onNodeSelect={selectObject}
				>
				
				{objectList.root.map(key => renderTree(key))}
			</TreeView>}
		</Paper>
	);
});

/*
 *
 */
const Item = props => {
	const { children, ...otherProps } = props;
	const classes = useStyles();
	
	return (
		<TreeItem
			classes={{
				root: classes.TreeItem,
				content: classes.TreeItemContent,
				expanded: classes.TreeItemExpanded,
				selected: classes.TreeItemSelected,
				group: classes.TreeItemGroup,
				label: classes.TreeItemLabel,
			}}
			
			{...otherProps}
			>
			
			{children || null}
		</TreeItem>
	);
}

/*
 *
 */
const Entry = props => {
	const { id, type, group, common } = props;
	const classes = useStyles();
	
	const name = common && common.name ? common.name : '';	
	const EntryIcon = group ? GroupIcon : StateIcon;
	return (
		<div className={clsx(classes.Entry, group && classes.Group)}>
			<EntryIcon color="inherit" className={classes.EntryIcon} />
			<EntryText width="40%" caption label={id} />
			{name.length > 60 ? 
				<Tooltip title={name} arrow>
					<div style={{ width: '40%' }}>
						<EntryText label={name.substr(0, 60) + '...'} />
					</div>
				</Tooltip>
			:
				<EntryText width="40%" label={name} />
			}
			<EntryText width="10%" label={type} />
			<EntryText width="10%" label={common && common.role} />
		</div>
	);
}

/*
 *
 */
const EntryText = props => {
	const { label, width, caption } = props;
	const classes = useStyles();
	
	return (
		<div style={{ width: width || '100%' }}>
			<Typography variant={caption ? 'caption' : 'body2'} className={classes.EntryText}>
				{(label && label[i18n.getLanguage()]) || label || ''}
			</Typography>
		</div>
	);
}

/*
 *
 */
export const StateExplorerPopup = React.memo(props => {
	const { open, _onClose, _onChange } = props;
	
	const state = useRef(props.val);
	return (
		<Dialog
			header={i18n.t('State Explorer')}
			actions={[
				<Inputs.Button key='action-cancel' variant="text" onClick={_onClose} color="secondary" label="Cancel" />,
				<Inputs.Button key="action-save" variant="text" onClick={() => _onChange(state.current)} color="primary" label="Apply" />
			]}
			
			dense
			maxWidth='lg'
			fullWidth={true}
			open={open}
			>
			
			<StateExplorer {...props} _onChange={s => state.current = s} />
		</Dialog>
	);
});

export default StateExplorer;
